import React, {useState, useEffect} from 'react';
import '../App.css';
import Board from './Board';
import Card from './Card';
import Footer from './Footer';
import Img from '../utilities/about.jpg';


function About(){



    useEffect(() => {
        fetchItems();
    },[]);

    const [items, setItems] = useState([]);

    const fetchItems =  async() => {
        const data = await fetch('https://api.thecatapi.com/v1/images/search');
        const items = await data.json();
        console.log(items);
        
        setItems(items);
    }

  return(

    <div className="About">
    <h2>About ME</h2>
    <div className="containerAbout">
    <img src={Img} alt="img"></img>
    <p className="hi"><span>Hi</span>, my name is <b>Andrei</b> and I'm trying to become a proffesional <b>Web Developer</b>. </p>
   <p className="focus"> My <b>focus</b> at the moment is to grow my skills in the <b>Web 
   Programming Languages</b> that I know and also to exceed my limits by learning new ones.</p>
    </div>
 
   <div className="gap"></div>
   <div className="gap"></div>
    <div className="flexbox" >
    <Board id="board-1" className="board" title="Languages" left="20%" >
    <h3 disabled>Move!</h3>
    
    <Card id="card-1" className="card" draggable="true">
    <p>HTML</p>    
    
    </Card>
    <Card id="card-6" className="card" draggable="true">
    <p>CSS</p>    
    
    </Card>
    <Card id="card-5" className="card" draggable="true">
    <p>JAVASCRIPT</p>    
    
    </Card>
    <Card id="card-8" className="card" draggable="true">
    <p>PHP</p>    
    
    </Card>

    </Board>

    <Board id="board-2" className="board" title="Frameworks">
    <Card id="card-2" className="card" draggable="true" bounds="parent">
    <p>React</p>  
    </Card>
    <Card id="card-9" className="card" draggable="true" bounds="parent">
    <p>Laravel</p>  
    </Card>

    </Board>

    <Board id="board-3" className="board" title="Plans to learn" left="10%">
    <Card id="card-3" className="card" draggable="true" bounds="parent">
    Angular
    </Card>
    <Card id="card-4" className="card" draggable="true" bounds="parent">
    <p>ASP.Net</p>    
    </Card>

    <Card id="card-7" className="card" draggable="true">
    {items.map(item =>(
       <img  src={item.url} alt={item.name} ></img>
      
     
   ))}
   </Card>

    </Board>
 
    </div>
   <div className="gap"></div>



   <Footer/>
    </div>
    
  
  );
}

export default About;